<template>
  <div class="navbar">
    <NavItem class="navitem firstitem" :pressed="pressedItem" name="home"   @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="event" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="order" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="realtime" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="patrol" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="prerun" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="device" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="user" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="consumption" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="dataquery" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="log" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="knowledge" @click="navhandler"></NavItem>
    <NavItem class="navitem" :pressed="pressedItem" name="systemconfig" @click="navhandler"></NavItem>
  </div>
</template>

<script>
  import NavItem from './NavItem'
  export default {
    name: 'nav',
    data () {
      return {
        // 
        pressedItem: ''
      }
    },
    methods: {
      navhandler: function (componentName) {
        // 
        this.$router.push({name: componentName})
        // 
        this.pressedItem = componentName
      }
    },
    watch: {
      // url
      '$route' (to, from) {
        this.pressedItem = to.name
      }
    },
    created: function () {
      // url
      this.pressedItem = this.$route.name
    },
    components: {NavItem}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .navbar {
    background-image:url('./navbarAssets/navbackground.png');
    background-repeat:no-repeat;
  }
  .navitem {
    float:left;
    height: 100%;
  }
  .firstitem {
    margin-left: 13px;
  }
</style>



